    // 是否已认证，用在是否可以选择头像和跳转到红娘帮忙页面
    get isAuthorized_user() {
        return this.userRoles.indexOf('authorized_user') !== -1;
    }

    get baseInfo() {
        const sex = this.userInfo.sex === 0 ? '男' : '女';
        const arr = [];

        if (this.params.action === 'findMatchmaker') {
            // 红娘榜只显示 常住地、工作单位、职务
            this.userInfo.company && arr.push(this.userInfo.company);
            this.userInfo.permanentPlace && arr.push(this.userInfo.permanentPlace);
            this.userInfo.duty && arr.push(this.userInfo.duty);
        } else {
            arr.push(sex);
            this.userInfo.company && arr.push(this.userInfo.company);
            this.userInfo.permanentPlace && arr.push(this.userInfo.permanentPlace);
            this.userInfo.education && arr.push(EDUCATION_OPTIONS[this.userInfo.education]);
            this.userInfo.school && arr.push(this.userInfo.school);
            
            if (this.userInfo.loveState === 0) {
                this.userInfo.age && arr.push(this.userInfo.age + '岁');
            }
        }

        return arr.join(' · ');
    }

    /**
     * 红娘好评星星数组
     */
    get stars() {
        return Utils.getStarsGrade(Math.round(this.topComment.avgGrade));
    }

    get recommendationRate() {
        const payRecommend = [];
		const freeRecommend = [];
		        
        if (this.letter) {
            for (const referer of this.letter.referers) {
                if (referer.subtaskId === null) {
                    // 免费推荐
                    freeRecommend.push(referer.refererInfo.realname);
                } else {
                    // 任务推荐
                    payRecommend.push(referer.refererInfo.realname || '系统推荐');
                }
            }

            const stars = Utils.getStarsGrade(Math.round(this.letter.avgIndex));
            return {
                stars,
                payRecommend: payRecommend.join(', '),
                freeRecommend: freeRecommend.join(', ')
            };
        }
        return null;
    }

    /**
     * 自我描述
     */
    get selfDescription() {
        // 红娘榜进入无法查看红娘个人信息
        if (this.params.action === 'findMatchmaker') {
            return null;
        }

        if (this.userInfo) {
            const criteriaVo = this.userInfo.criteriaVo;
            let arr = [];
            this.userInfo.achievements ? arr = arr.concat(this.userInfo.achievements) : null;
            this.userInfo.overseasExperiences ? arr = arr.concat(this.userInfo.overseasExperiences) : null;
            this.userInfo.socialActivitys ? arr = arr.concat(this.userInfo.socialActivitys) : null;
            // this.userInfo.specialExperiences ? arr.concat(this.userInfo.specialExperiences) : null;
            this.userInfo.otherExperiences ? arr = arr.concat(this.userInfo.otherExperiences) : null;

            let idealObejcts = criteriaVo.idealObejcts ? criteriaVo.idealObejcts.slice(0, 6) : [];
            // 判断对方是否是关闭理想对象显示
            if (this.userInfo && this.userInfo.uid !== this.myInfo.uid && this.userInfo.switch2 === 0) {
                idealObejcts = [];
            }
            const selfDescription = {
                idealObejcts,
                monologue: this.userInfo.monologue ? this.userInfo.monologue : this.params.action === 'me' ? '添加' : '暂时没有自我评价',
                achievementTags: arr.slice(0, 4)
            };
            return selfDescription;
        }
        return null;
    }

    // 浮动按钮
    FLOATBTN_TXT = {
        SEND_MESSAGE: '发消息',
        ADD_FRIEND: '加好友',
        RECOMMEND: '帮TA牵线',
        FIND_MATCHMAKER: '请TA帮忙',
        IGNORE: '不再推荐'
    };

    get leftFloatBtnTxt() {
        // 自己页面隐藏按钮
        if (this.isMySelf || !this.userInfo) {
            return '';
        } else if (this.params.action === 'findMatchmaker' || this.params.action === 'otherUser') {
            if (Utils.isMyFriends(this.userInfo.uid)) {
                // 已经是好友
                return this.FLOATBTN_TXT.SEND_MESSAGE;
            } else {
                // 不是好友
                return this.FLOATBTN_TXT.ADD_FRIEND;
            }
        } else if (this.params.action === 'recommendation') {
            return this.FLOATBTN_TXT.ADD_FRIEND;
        } else {
            return '';
        }
    }

    get rightFloatBtnTxt() {
        // 自己页面隐藏按钮
        if (this.isMySelf) {
            return '';
        } else if (this.params.action === 'findMatchmaker') {
            return this.FLOATBTN_TXT.FIND_MATCHMAKER;
        } else if (this.params.action === 'otherUser') {
            return this.FLOATBTN_TXT.RECOMMEND;
        } else if (this.params.action === 'recommendation') {
            return this.FLOATBTN_TXT.IGNORE;
        } else {
            return '';
        }
    }

