    get isShowHome() {
        if (this.params.action !== 'me' && this.query && this.query['from']) {
            return true;
        } else {
            return false;
        }
    }

    // 判断是否是自己，以便隐藏某些信息
    get isMySelf() {
        if (this.userInfo && this.myInfo && this.userInfo.uid === this.myInfo.uid) {
            return true;
        } else {
            return false;
        }        
    }
    // 判断是否默认头像
    get isDefaultHearpoit() {
        if (this.userInfo && this.userInfo.headPortrait.indexOf('DefaultHeadPortraits') === -1) {
            return false;
        } else {
            return true;
        }
    }

    get hasTab() {
        if (this.params.action === 'me') {
            if (this.isCroping) {
                return false;
            } else {
                return true;
            }
        } else {
            return false;
        }
    }

    async activated() {
        this.isUploading = false;
        this.letter = null;
        this.isCroping = false;

        // 等待个人资料初始化完毕
        await Utils.initedUser();
        console.log('activated');
        
        const params: IPersonalParams = this.$route.params;

        const query = this.$route.query;
        console.log(query);
        this.query = query;

        console.log(params);
        const action = params.action || 'me';
        const id = params.id || this.$store.state.user.user.uid;
        this.params = {
            action,
            id
        };

        try {
            switch (action) {
            case 'me': 
                this.updateTabValue('me');
                this.initMyInfo();
                break;
            case 'otherUser': // 帮TA牵线
                this.initOtherUserInfo();
                break;
            case 'newFriend': // 好友申请
                this.initOtherUserInfo();
                break;
            case 'recommendation': // 加好友
                this.initRecomendationInfo();
                break;
            case 'findMatchmaker': // 请TA帮忙
                this.initFindMatchmakerInfo();
                break;
            case 'redLine':
                this.initRedLineInfo();
            }
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        }
    }

    deactivated() {
        // console.log('deactivated');
        // const viewer = this.$refs['viewer'].$viewer;
        // viewer.hide();
        // viewer.destroy();

        // 关闭裁剪页面
        if (this.isCroping) {
            this.hideCroppa();
        }
        
        if (this.isUploading) {
            this['destoried']();
        }

        // 关闭图片预览
        const opt: IViewImageOpt = {
            showIndex: 0,
            isShow: false
        };
        this.toggleView(opt);
    }

    /* 数据初始化 */
    async initMyInfo() {
        await Utils.initedUser();
        this.userInfo = this['$store'].state.user.user;
        // this.getAlbum();
        this.getTopFriendImpression();
        this.getTopTaskComment();

        // 微信分享配置
        this.initWX();
    }

    initOtherUserInfo() {
        this.getOtherUserInfo();
        this.getTopFriendImpression();
        this.getTopTaskComment();
        this.getCommonPoint();
    }

    initRecomendationInfo() {
        this.getOtherUserInfo();
        // 获取推荐指数，需要从推荐信列表缓存进来
        this.letter = Utils.getItem(CACHE_NAME.letter + this.params.id);
        this.getTopFriendImpression();
        this.getCommonPoint();
    }

    // 从红娘榜进入
    initFindMatchmakerInfo() {
        this.getOtherUserInfo();
        this.getTopFriendImpression();
        this.getTopTaskComment();
        this.getCommonPoint();
    }

    // 从牵红线列表进入
    initRedLineInfo() {
        this.getOtherUserInfo();
        this.getTopFriendImpression();
        this.getTopTaskComment();
        this.getCommonPoint();
    }

    /********* 微信分享配置  ******* */
    async initWX() {
        let url: string;
        let title: string;
        let desc: string;
        let imgUrl: string;
        let inviteCode: string;

        if (this.params.action === 'redLine') {
            // 悬赏榜进入
            Utils.initWX();
            // 分享到 link 悬赏榜，无动作
            url = Utils.getShareUrl('/tab/link');
            title = TITLE_INVITE + '优秀单身 - ' + this.userInfo.realname;
            desc = SHARE_DESC_1;
            imgUrl = Utils.getThumbnailImg(this.userInfo.headPortrait);

        } else if (this.params.action === 'recommendation') {
            // 从推荐信进来
            inviteCode = await Utils.initWX(INVITE_ACTION_GROUP.CHANGE_AND_ADD);
            // 分享到 ai，单身进入推荐信 非单身进入悬赏榜，弹窗加好友
            url = Utils.getShareUrl('/tab/ai', inviteCode);
            title = TITLE_FRIEND_INVITE + this.myInfo.realname;
            desc = SHARE_DESC_1;
            imgUrl = Utils.getThumbnailImg(this.myInfo.headPortrait);

        } else if (this.params.action === 'findMatchmaker') {
            // 从红娘榜进入
            Utils.initWX();
            // 分享到 红娘个人主页， 无动作
            url = Utils.getShareUrl('/personal/findMatchmaker/' + this.userInfo.uid);
            title = TITLE_INVITE + '靠谱红娘 - ' + this.userInfo.realname;
            desc = SHARE_DESC_1;
            imgUrl = Utils.getThumbnailImg(this.userInfo.headPortrait);

        } else if (this.params.action === 'me') {
            // 个人主页
            inviteCode = await Utils.initWX(INVITE_ACTION_GROUP.ADD_FRIEND);
            // 分享到 个人主页 弹窗加好友
            url = Utils.getShareUrl('/personal/otherUser/' + this.myInfo.uid);
            title = TITLE_FRIEND_INVITE + this.myInfo.realname;
            desc = SHARE_DESC_1;
            imgUrl = Utils.getThumbnailImg(this.myInfo.headPortrait);
        } else { //  if (this.params.action === 'otherUser' || this.params.action === 'newFriend') 
            // 查看他人主页 或者 是进入申请好友主页
            inviteCode = await Utils.initWX(INVITE_ACTION_GROUP.CHANGE_AND_ADD);
            // 分享到 ai 单身进入推荐信，非单身进入悬赏榜，弹窗加好友
            url = Utils.getShareUrl('/tab/ai', inviteCode);
            title = TITLE_FRIEND_INVITE + this.myInfo.realname;
            desc = SHARE_DESC_1;
            imgUrl = Utils.getThumbnailImg(this.myInfo.headPortrait);
        } 

        Utils.setWXShareConfig(url, title, desc, imgUrl);
    }

