    /************************************** */
    /**
     * 获取他人用户信息
     */
    async getOtherUserInfo() {
        let res;
        if (this.params.action === 'recommendation') {
            res = await Api.getOtherUserInfoByLetter(this.params.id);
        } else {
            res = await Api.getOtherUserInfo(this.params.id);
        }
        this.userInfo = res.body.obj;

        // 分享配置
        // this.initShare();
        this.initWX();
    }

    // 获取好友印象
    async getTopFriendImpression() {
        let res;
        if (this.params.action === 'recommendation') {
            res = await Api.getTopFriendImpressionByLetterId(this.params.id);
        } else {
            res = await Api.getTopFriendImpression(this.params.id);
        }
        this.topFriendImpression = {
            topSentences: res.body.obj.contents,
            topSentenceTags: res.body.obj.tags
        };
    }

    // 红娘指数
    async getTopTaskComment() {
        const res = await Api.getTopTaskComment(this.params.id);
        this.topComment = res.body.obj;
        this.topComment.avgGrade === null ? this.topComment.avgGrade = 0 : '';
        // this.topComment.avgGrade = 3;
    }

    // 获取共同点
    async getCommonPoint() {
        // if (this.userInfo && this.userInfo.uid === this.myInfo.uid) return;        
        let res;
        if (this.params.action === 'recommendation') {
            res = await Api.getCommonPointByLetterId(this.params.id);
        } else {
            res = await Api.getCommonPoint(this['$store'].state.user.user.uid, this.params.id);
        }
        this.commonPoint = res.body.obj;
    }

    /**
     * 浏览头像
     */
    viewAvatar() {
        // const viewer = this.$refs['viewer'].$viewer;
        // viewer.show();
        this.updateViewImgs([this.userInfo.headPortrait]);
        const opt: IViewImageOpt = {
            isShow: true,
            showIndex: 0
        };
        this.toggleView(opt);
    }

    toCommonPage() {
        const params: ICommonPointParams = this.params;
        this['$router'].push({
            name: 'commonPoint',
            params
        });
    }

    toMyTaskPage() {
        this['$router'].push({
            name: 'myTaskList'
        });
    }

    toFriendCommentPage() {
        if (this.params.action === 'me') {
            this['$router'].push({
                name: 'commentView',
                params: {
                    action: 'me',
                    id: this.userInfo.uid
                }
            });
        } else {
            const params = this.params;
            this['$router'].push({
                name: 'commentView',
                params
            });
        }                
    }

    /**
     * 进入个人信息页
     * @param index 0进入择偶条件 1进入基本信息 2进入内心独白 3进入成就标签
     */
    toPersonalInfoPage(index: string) {
        /**
         * me 可以查看
         * newFriend recommendation可以查看个人信息
         * otherUser 非好友无法查看个人信息
         * redLine 非好友无法查看
         */
        if (this.params.action === 'otherUser' || (this.params.action === 'redLine' && !this.isMySelf)) {
            if (!Utils.isMyFriends(this.params.id)) {
                return;
            }
        }

        if (this.isMySelf) {
            Utils.saveItem(CACHE_NAME.updateUser, this.userInfo);
        } else {
            if (this.userInfo.switch2 === 0 && index === '0') {
                // 关闭理想对象
                index = '1';
            }
        }
        // 查看别人理想对象先检查是否被关闭
        const params: IPersonalInfoParams = {
            action: this.params.action,
            id: this.params.id,
            index
        };

        this['$router'].push({
            name: 'personalInfo',
            params
        });
    }

    toSettingPage() {
        this['$router'].push({
            name: 'setting'
        });
    }

