    async clickRightFloatButton() {
        const txt = this.rightFloatBtnTxt;
        if (txt === this.FLOATBTN_TXT.FIND_MATCHMAKER) {
            // 请ta帮忙，先检查自己是不是单身
            if (this.myInfo.loveState === 0) {
                // 单身
                this.resetSelectedAcquaintanceList([
                    {
                        toUid: this.userInfo.uid,
                        realname: this.userInfo.realname,
                        amount: this.userInfo.matchmakerInfo.price,
                        headPortrait: this.userInfo.headPortrait
                    }
                ]);
                const params: IAcquaintanceTaskParams = {
                    action: 'fromFindMatchMaker',
                    id: this.userInfo.uid
                };
                this['$router'].push({
                    name: 'acquaintanceTask',
                    params
                });
            } else {
                // 非单身
                this.showToast(new ToastMsg(FIND_MATCHMAKER_HINT));
            }
        } else if (txt === this.FLOATBTN_TXT.RECOMMEND) {
            // 帮ta牵线
            if (this.userInfo.loveState === 0) {
                // 单身
                const params: IRecommendParams = {
                    action: 'person',
                    uid: this.userInfo.uid,
                    subId: '0'
                };
                this['$router'].push({
                    name: 'recommend',
                    params
                });
            } else {
                // 非单身
                this.showToast(new ToastMsg('对方是非单身身份，无法帮TA牵线'));
            }
        } else if (txt === this.FLOATBTN_TXT.IGNORE) {
            // 忽略
            try {
                await Api.noMoreRecommend(this.params.id);
                this.showToast(new ToastMsg('该用户已从推荐列表移除'));
            } catch (error) {
                this.showToast(new ToastMsg(error.message));
            }
        }   
    }

